import { Component, OnInit, Input, ElementRef, ViewChild } from '@angular/core';
import { StorageWebService } from 'src/app/Services/Storage/StorageWeb.service';
import { ModalController } from '@ionic/angular';
import { ApiService } from 'src/app/Services/Api/Api.service';
import { ToastService } from 'src/app/Services/Utilities/Toast.service';
import { UploadFileService } from 'src/app/Services/Repository/uploadFile.service';
import { map, catchError } from 'rxjs/operators';
import { HttpEventType, HttpErrorResponse } from '@angular/common/http';
import { of } from 'rxjs';

@Component({
  selector: 'app-category',
  templateUrl: './category.page.html',
  styleUrls: ['./category.page.scss'],
})
export class CategoryPage implements OnInit {
  @Input() type;
  @Input() listPermiss;
  @Input() idCategory;
  @Input() title;
  @Input() parentId;

  // With Alter Category
  @Input() categoryDatAlter;

  category: any = {
    name: '',
    description: '',
    company: ''
  };

  folders: any = {
    name: '',
    description: '',
    parent: ''
  };

  users: any[] = [];
  customers = [];
  usersSelect = [];

  customer: any = {
    name: '',
    login: '',
    clave: '',
    company: '',
    userID: ''
  };

  textFilter = '';


  @ViewChild('fileUpload', {static: false}) fileUpload: ElementRef;
  files  = [];
  count = 0;
  constructor(
    private storage: StorageWebService,
    private modalCtrl: ModalController,
    private apiService: ApiService,
    private myToast: ToastService,
    private uploadService: UploadFileService
  ) { }

  ngOnInit() {
  }


  async ionViewWillEnter() {
    this.initModal();
  }

  async initModal() {
    if (this.type === 'permiss') {
      this.usersSelect = this.listPermiss;

      const login = await this.storage.getLogin();
      if (login) {
        console.log(login[0]);
  
        this.apiService.getCustomersCompany(login[0].CompanyID).subscribe((data: any) => {
          if (data.status === 'ok') {
  
            if (data.response.length > 0) {
              data.response.forEach((item) => {
                item.ID = item.login;
                this.customers.push(item);
              })
            }
            this.customers = data.response;
          }
  
        });
  
        this.apiService.getUsersCompany({AccessToken: login[0].AccessToken}).subscribe((data: any) => {
  
          data = JSON.parse(data);
          console.log(data);
          this.users = data[0];
  
          this.users = this.users.filter((item) => {
            return item.ID !==  login[0].UserID;
          });
  
  
        });
  
  
    }
    } else if (this.type === 'alter') {
      console.log(this.categoryDatAlter, 'iloves');
      this.category.name = this.categoryDatAlter.name;
      this.category.description = this.categoryDatAlter.description;
    }
  }

  selection( event ) {

    if (event.detail.checked) {
      this.usersSelect.push(event.detail.value);
    } else {
      this.usersSelect = this.usersSelect.filter((item) => item !== event.detail.value);
    }

    console.log(this.usersSelect);
  }

  savePermiss() {
    this.apiService.alterPermiss({idCategory: this.idCategory, listPermiss: this.usersSelect}).subscribe((data: any) => {
      console.log(data);
      if (data.status === 'ok') {
        this.myToast.newCreatedToast('Permisos actualizados para esta categoria', true);
      } else {
        this.myToast.newCreatedToast('No se pudo actualizar los permisos, intentelo mas tarde', false);
      }

      this.cancelModal();
    })
  }

  filterText( event ) {
      this.textFilter = event.detail.value;
  }



  async uploadFile(file) {
    const Login = await this.storage.getLogin();

    if (Login) {
      const formData = new FormData();
      formData.append('archivo', file.data);
      file.inProgress = true;
      this.uploadService.upload(formData, Login[0].CompanyID, 'file', this.parentId).pipe(
        map((event: any ) => {
          switch (event.type) {
            case HttpEventType.UploadProgress:
              file.progress = Math.round(event.loaded * 1 / event.total);
              break;
            case HttpEventType.Response:
              return event;
          }
        }),
        catchError((error: HttpErrorResponse) => {
          file.inProgress = false;
          return of(`${file.data.name} upload failed.`);
        })).subscribe((event: any) => {
          if (typeof (event) === 'object') {
            console.log(event.body, ' Respuesta');

            if (event.body.status === 'ok') {
              this.count--;
              if (this.count === 0) {
                this.modalCtrl.dismiss({
                  status: true,
                  message: 'Archivo subido con éxito'
                });
              }
              
            } else {
              this.modalCtrl.dismiss({
                status: false
              });
            }
            
          }
        });
    }
  }

  private uploadFiles() {
    this.count = this.files.length;
    this.fileUpload.nativeElement.value = '';
    this.files.forEach(file => {
      this.uploadFile(file);
    });
}

  onClick() {
    const fileUpload = this.fileUpload.nativeElement;
    fileUpload.onchange = () => {
    // tslint:disable-next-line:prefer-for-of
    for (let index = 0; index < fileUpload.files.length; index++) {
     const file = fileUpload.files[index];
     this.files.push({ data: file, inProgress: false, progress: 0});
    }
    this.uploadFiles();
    };
    fileUpload.click();
}


  async saveCategory() {
    const Login = await this.storage.getLogin();

    if (Login) {
      if (this.type === 'category') {
        this.category.company = Login[0].CompanyID;
        this.category.userID = Login[0].type === 'inter' ? Login[0].UserID : Login[0].login;
        this.category.listPermiss = Login[0].type === 'inter' ? [Login[0].UserID] : [Login[0].login];
        console.log(this.category);
        this.apiService.createdCategory(this.category).subscribe((data: any) => {
          console.log(data);
          if (data.status) {
            this.modalCtrl.dismiss({
              st: true
            });
            this.myToast.newCreatedToast('Categoria creada correctamente ', true);
          } else {
            this.myToast.newCreatedToast('Error al crear la categoria ', false);
          }
        }, (err) => {
            this.myToast.newCreatedToast('Error al crear la categoria ', false);
        });
      } else if (this.type === 'alter') {
        // tslint:disable-next-line:max-line-length
        this.apiService.alterCategory({idCategory: this.categoryDatAlter._id, name: this.category.name, description: this.category.description}).subscribe((data: any) => {
          console.log(data);
          if (data.status === 'ok') {
            this.modalCtrl.dismiss({
              st: true
            });
            this.myToast.newCreatedToast('Categoria editada correctamente ', true);
          } else {
            this.myToast.newCreatedToast('No se pudo editar la categoria, intentelo más tarde ', false);
          }
        }, (err) => {
            this.myToast.newCreatedToast('No se pudo editar la categoria, intentelo más tarde ', false);
        });
      }
    }

  }



  async saveFolder() {

      console.log(this.folders);
      this.folders.parent = this.parentId;
      this.apiService.createdFolder(this.folders).subscribe((data: any) => {
        console.log(data);
        if (data.status) {
          this.modalCtrl.dismiss({
            status: true,
            message: 'Carpeta creada con éxito'
          });
          this.myToast.newCreatedToast('Carpeta creada correctamente ', true);
        } else {
          this.modalCtrl.dismiss({
            status: false
          });
          this.myToast.newCreatedToast('Error al crear la carpeta ', false);
        }
      }, (err) => {
          this.myToast.newCreatedToast('Error al crear la carpeta ', false);
      });

  }

  cancelModal() {
    this.modalCtrl.dismiss({
      status: false
    });
  }

}
